body {
  background: #293950;
  font-family: Poppins, sans-serif;
}

.app {
  width: 90vw;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}

.input {
  width: 100%;
  text-align: center;
  margin: 20px 0;
}

.input input {
  padding: 10px 0 10px 10px;
  font-family: inherit;
  color: white;
  background-color: transparent;
  border: 1px solid #33527c;
  border-radius: 30px;
  outline: none;
  margin-right: 20px;
}

.input button {
  background-color: #3a76c9;
  border-radius: 30px;
  padding: 10px 20px;
  border: none;
  outline: none;
  color: white;
  font-size: 16px;
  cursor: pointer;
  transition: all .3s ease-in-out;
}

.input button:hover {
  background-color: rgb(12, 88, 196);
}

.image-container {
  display: flex;
  width: 30%;
  height: 400px;
  margin: 10px auto;
  position: relative;
  cursor: pointer;
}

.image {
  display: block;
  max-width: 100%;
  object-fit: cover;

  transition: 0.5s ease;
}

.image:hover {
  opacity: 0.5;
}

.info {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 10px;
  left: 10px;
}

.avatar {
  max-width: 100%;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.name {
  color: whitesmoke;
  padding: 0;
  margin: 0;
}

@media (max-width: 767px) {
  .image-container {
    width: 100%;
    height: 50vh;
  }
}
